import React from 'react';
import { n } from './helpers.jsx';
import { BarChart, Bar, XAxis, YAxis, Tooltip, ResponsiveContainer } from 'recharts';

export default function DailyDot(data) {
  const htvls = data?.contributions;
  const finalhtvls = Object.entries(htvls);
  const htvld = finalhtvls?.map(function(item, i){
    return { 
      date: item[0].substring(0,6),
      val : item[1]/10000000000,
    }
  })


  const renderLegendToolDot = (props) => {
    const { payload } = props;
  
    return (
          payload?.map((entry, index) => (
            <p key={`item-${index}`} className="giant">
              <strong>{n(entry.payload.val)} DOT </strong>
              <small>({entry.payload.date})</small>
            </p>
          ))
    );
  }

  return (
    <>
      <div className="dailydot-container">
        <p className="giant porcentshare ">
          {n(htvld[htvld.length -1].val)} DOT <small>({htvld[htvld.length -1].date})</small>
        </p>
        <div className="dailydot">
          <ResponsiveContainer className="daily-dots" width="100%" height="100%" >
            <BarChart
              width="100%"
              height={300}
              data={htvld}

              margin={{
                top: 5,
                right: 0,
                left: 20,
                bottom: 5,
              }}
              >
              <defs>
                <linearGradient id="colorUvb" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="5.23%" stopColor="#0C3EE3" stopOpacity={0.9}/>
                  <stop offset="57.88%" stopColor="#0C3EE3" stopOpacity={0.95}/>
                  <stop offset="97.88%" stopColor="#0C3EE3" stopOpacity={1}/>
                  
                </linearGradient>
              </defs>
              <XAxis dataKey="date" tick={{fontSize: 13}}  />
              <YAxis tick={{fontSize: 13}} orientation="right" tickFormatter={(value) => new Intl.NumberFormat('en', { notation: "compact" , compactDisplay: "short" }).format(value)} minTickGap={20}/>
              <Tooltip content={renderLegendToolDot}/>
              <Bar dataKey="val" stroke="#0C3EE3" fill="url(#colorUvb)" radius={[4, 4, 0, 0]}/>
            </BarChart>
          </ResponsiveContainer>
        </div>
      </div>
    </>
  )
}
