import React from 'react';
import _ from 'lodash';

export default function ProgressBar(data) {

  const bars     = data?.bars;
  const paraId   = data?.paraId;
  const raised   = data?.raised;
  let   ready    = false;
  let   final    = false;

  const w  = data.width,
        b  = data.bar,
        dd = (100-((b*100))).toFixed(2);
  const presentBar = Object.values(bars)?.filter(c => (c.name !== "Parallel" && c.name !== 'Others'))
  const parallelP  = 'Parallel: ' + (b*100).toFixed(2) + '%';
  ready = Object.keys(presentBar).length === 7;

  if(ready) {

    final = presentBar.map((entry, index) => {

      let picker = _.pickBy(entry.data, (value, key) =>
        _.some([paraId], str => _.includes(key, str))
      )
        return Object.keys(picker).length === 0 ? '' : {
          ids : Object.keys(picker)[0].substring(0,4),
          name : entry.name,
          color : entry.color,
          picker : picker[Object.keys(picker)[0]]
        }
    })

  }

  let OtherPorcent = 0;
  let finalToltip  = '';
  return !ready ? (<div className="loader"><div></div><div></div><div></div><div></div></div>) : (
    <>
      <div className="meter" style={{width: 800*w }} >
        <span style={{width: b*100 + '%'}} />
        
        { ready &&
          final?.sort((a, b) => b.picker - a.picker).map((entry, index) => {
            if(entry.ids) {
              if(entry.ids === paraId) {
                OtherPorcent = OtherPorcent + ((entry.picker/raised)*100);
                finalToltip  = finalToltip+entry.name+': '+((entry.picker/raised)*100).toFixed(2) + '%'+'\n';
                return(
                  <>
                    <span 
                    key={index}
                    style={{
                      width: ((entry.picker/raised)*100).toFixed(2) + '%',
                      backgroundColor:entry.color
                    }} 
                    />
                  </>
                ) 
              }
                         
            }
          })
        }

        <span style={{width: dd + '%'}}/>
        <span className="invisible" 
          data-tooltip={
            finalToltip.length ? 
            parallelP + '\n' + finalToltip + 'Others: '+ (dd-OtherPorcent).toFixed(2) +'%' : 
            parallelP + '\n' + 'Others: ' + dd + '%'
          } />
      </div>
    </>
  )

}