import React, {useEffect, useState} from 'react';
import _ from 'lodash';
import Select from 'react-select';
import Logo from '../components/Logo';
import ProgressBar from '../components/ProgressBar';
import { n, m , Porcent } from '../data/helpers.jsx';

export default function CrowdloanByProject(data) {
  const [AllCrowdloans        , setCrowdloans        ] = useState([]);
  const [showStatus           , setshowStatus        ] = useState("auction611");

  const raising       = data?.raising?.sort((a, b) => b.raised - a.raised);
  const winners       = raising?.filter(fil => (fil.status === "Won" && fil.wonAuctionId <= 5));
  const nonWinners    = raising?.filter(a => (a.status !== "Won" || a.wonAuctionId >= 6 || !a.wonAuctionId));
  const contrib       = data?.contrib;
  const parachains    = data?.parachains;
  const markets       = data?.markets;


  useEffect(()=>{
    setCrowdloans(nonWinners)
  }, [])

  const filterCrowdloan = (props) => {
    switch (props) {

      case "all": 
        setCrowdloans(raising) 
        setshowStatus("all")
      break
      case "auction15":
        setCrowdloans(winners)
        setshowStatus("auction15")
      break;
      case "auction611":
        setCrowdloans(nonWinners)
        setshowStatus("auction611")
      break;
    }
 }


  return (
    <>
      <div className="selectors">
        <p>
          <span><i className="gray"/> Total DOT Raised </span>
          <span><i className="blue"/> Raised on Parallel </span>
        </p>
        <div className="filterDetails"> 
          <Select
              options={[{label:"All", value:"all"},{label:"AUCTIONS #1-5", value:"auction15"},{label:"AUCTIONS #6-11", value:"auction611"}]}
              defaultValue={{ label: "AUCTIONS #6-11", value: "auction611" }}
              onChange={event => filterCrowdloan(event.value)}
            />
        </div>
      </div>
      <div>
        { 
          AllCrowdloans?.map((index) => {
            
            const id          = index.paraId,
                  r           = index.raised,
                  t           = AllCrowdloans[0].raised,
                  p           = parachains?.filter(i => i.paraid === id),
                  placeholder = p?.length === 1 ? true : false,
                  item        = raising?.filter(g => g.paraId === id)[0],
                  gTotal      = item.parallelRaised/r,
                  rt          = r/t;
            return (

              <div  key={id} id={id}>
                <div className="flex">
      
                  { parachains &&
                    placeholder
                    ? <Logo key={index.paraId} src={p[0]}/>
                    : <Logo key={index.paraId} src={{'icon':'https://crowdloan.parallel.fi/static/media/polkadot.7e142f48.svg'}}/>
                  }
   
                    <div className="crowdloan-by">
                      <ProgressBar key={index.paraId+'bar'} width={rt} raised={r} bar={gTotal} bars={markets} paraId={item?.paraId}/>
                      <div>
                        <p className="marketData">
                          <strong> 
                            { r ? m(r) : '0' } 
                          </strong> | <span> 
                            { Porcent(gTotal) + '% (' + m(item?.parallelRaised) + ')' } 
                          </span>  
                        </p>
                      </div>
                    </div>
                  
                </div>
              </div>
              
            )
        } )
        }
      </div>
    </>
  )
}