import React, { useEffect, useState } from 'react';
import { n } from './helpers.jsx';
import { AreaChart, Area, XAxis, Tooltip, ResponsiveContainer } from 'recharts';

export default function Htvl(data) {
  const htvls = data?.data;
  const finalhtvls = Object.entries(htvls);
  const tvl = data?.tvl;
  const htvld = finalhtvls.map(function(item, i){
    return { 
      date: item[0].substring(0,6),
      val : item[1]/10000000000,
    }
  })


  const [result        , setResult        ] = useState();

  useEffect(()=>{
    
    const dotHistorical = "https://min-api.cryptocompare.com/data/v2/histoday?fsym=DOT&tsym=USD&limit="+Object.keys(finalhtvls).length;
    fetch(dotHistorical).then(response => response.json())
    .then(data => {
      const dotPrice = data.Data.Data;
      const results  = htvld.reduce((r, value, i) => {
        const { val, date } = value
        const cumVal = i === 0 ? 0 : r[i - 1].val
        const newDatum = { date, val: val + cumVal }
        r.push(newDatum);
        return r;
    }, []).map(function(item, i){
      return { 
        date : item.date,
        val : item.val,
        usd : item.val*dotPrice[i].close,
      }
    })

    setResult(results);
    });
}, [])

const renderLegend = (props) => {
  const { payload } = props;

  return (
    payload?.map((entry, index) => (
      <p key={`item-${index}`} className="giant">
          <strong>${n((entry.payload.usd))} </strong>
          <small>({entry.payload.date})</small>
          <em>{n((entry.payload.val))} DOT</em>
      </p>
    ))
);
}


  return (
    <>
      {tvl && result &&
        <p className="giant porcentshare">
          <strong>${n(result[(result.length) - 1].usd) } </strong>
          <small>({result[(result.length) - 1].date })</small>
          <em>{ n(tvl.totalDot)} DOT </em>
        </p>
      }
      <ResponsiveContainer className="total-value" width="100%" height="100%">
        <AreaChart
          width={600}
          height={200}
          data={result}
          margin={{
            top: 10,
            left: 0,
            bottom: 0,
          }}
        >
        <defs>
          <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
            <stop offset="5.23%" stopColor="#0C3EE3" stopOpacity={1}/>
            <stop offset="7.34%" stopColor="#0C3EE3" stopOpacity={1}/>
            <stop offset="97.88%" stopColor="rgba(255, 255, 255, 0.64)" stopOpacity={1}/>
            
          </linearGradient>
        </defs>
          <XAxis dataKey="date" tick={{fontSize: 13}} />

          <Tooltip content={renderLegend}/>
          <Area type="monotone" dataKey="val" strokeWidth="2.5" stroke="#0C3EE3" fill="url(#colorUv)" />
        </AreaChart>
      </ResponsiveContainer>

    </>
  )
}
