import React, { useEffect, useState } from 'react';
import { n, m , Porcent } from '../../data/helpers.jsx';
import GlobalTvl from '../../data/globalTvl.jsx';
import GlobalDotTvl from '../../data/globalDotTvl.jsx';
import Htvl from '../../data/Htvl.jsx';
import Share from '../../data/share.jsx';
import CrowdloanByProject from '../../data/CrowdloanByProject.jsx';
import DailyDot from '../../data/DailyDot';
import DailyDotByProject from '../../data/DailyDotByProject';
import Dotmarket from '../../components/Dotmarket';
import MarketDominance from '../../components/MarketDominance'
import DotContributionsFilter from '../../data/DotGroups.jsx';
import _ from 'lodash';

export default function CrowdloanList(data) {

  const raising       = data?.raise?.filter(a => a.status !== "Expired")
  const parachains    = data?.parachain;
  const tvl           = data?.tvl;
  const htvl          = data?.htvl;
  const DotAmount     = data?.dotAmount; 
  const DailyContrib  = data?.dailyContrib;
  const marketshare   = [{}];

  const market  = raising.reduce(function(sum, current) { return parseInt(sum) + parseInt(current.raised); }, 0);
  const share   = raising.filter(a => a.status !== "Expired").filter(g => g.parallelRaised).reduce(function(sum2, current2) { return parseInt(sum2) + parseInt(current2.parallelRaised); }, 0);
  const cdots   = raising.filter(a => a.status === "Won").filter(g => g.parallelRaised).reduce(function(sum2, current2) { return parseInt(sum2) + parseInt(current2.parallelRaised); }, 0);
  marketshare.market = market;
  marketshare.share  = share;
  marketshare.porcent = Porcent(marketshare.share/marketshare.market);



  //To improve
    const [markets    , setMarkets    ] = useState({});
    const baseApi    = "https://auction-service-prod.parallel.fi/crowdloan/marketshare/";

    const getKucoin = async () => {
      const res = await fetch(baseApi+"13LHN6KoMJ2BFh12FeE1Lm1kGGT8V2SV3oH6Ftuk3LyqDUmN");
      const details = await res.json();
      const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
      setMarkets((net) => ({
        ...net,
        kucoin: {
          name: "Kucoin",
          value : finalData,
          data : details.data,
          color : "#23af91",
          market : ((finalData/market)*100).toFixed(2)
        }
      }));
    }
    
    const getBinance = async () => {
      const res = await fetch(baseApi+"1xhXb5ZjsKpN1xcvTNMuThndfb1WP9saLKMpNxofGm64U2X");
      const details = await res.json();
      const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
      setMarkets((net) => ({
        ...net,
        binance: {
          name: "Binance",
          value : finalData,
          data : details.data,
          color : "#f6cc27",
          market : ((finalData/market)*100).toFixed(2)
        }
      }));
    }

    const getKraken = async () => {
      const res = await fetch(baseApi+"1344zprT7A4VFrQ46GJycLrjhBoYjf1RcsjbRLmk5DtsYshV");
      const details = await res.json();
      const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
      setMarkets((net) => ({
        ...net,
        kraken: {
          name: "Kraken",
          value : finalData,
          data : details.data,
          color : "#5841d8",
          market : ((finalData/market)*100).toFixed(2)
        }
      }));
    }
    
    const getBifrost = async () => {
      const res = await fetch(baseApi+"14AMZ3gw4tRsrdp78i4MmHZ8EFbXTMfuXGQMEC3t1GoqLboH");
      const details = await res.json();
      const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
      setMarkets((net) => ({
        ...net,
        bifrost: {
          name: "Bifrost",
          value : finalData,
          data : details.data,
          color : "#cb5688",
          market : ((finalData/market)*100).toFixed(2)
        }
      }));
    }

    const getOkex = async () => {
      const res = await fetch(baseApi+"15abPBmJrMY7QJeCEQJQbQ9a62A7ndfTo8KC7Wn4dzt9zMMg");
      const details = await res.json();
      const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
      setMarkets((net) => ({
        ...net,
        okex: {
          name: "Okex",
          value : finalData,
          data : details.data,
          color : "#88bcf2",
          market : ((finalData/market)*100).toFixed(2)
        }
      }));
    }

    const getGate = async () => {
      
      const res = await fetch(baseApi+"13uWt4NnfTuVFxnqTQCryktasmFEFAGpndn19K1ZGxidf7XR");
      const details = await res.json();
      const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
      setMarkets((net) => ({
        ...net,
        gate: {
          name: "Gate.io",
          value : finalData,
          data : details.data,
          color : "#e64e62",
          market : ((finalData/market)*100).toFixed(2)
        }
      }));
    }

    const getEquilibrium = async () => {
      const res = await fetch(baseApi+"13rWYJ2DWj4ZQrZWfVJRJXqVSaQfd34QmRJrsJdSsApnnmK");
      const details = await res.json();
      const finalData = Object.entries(details.data)?.reduce(function(sum, current) { return parseFloat(sum + current[1]) }, 0);
      setMarkets((net) => ({
        ...net,
        equilibrium: {
          name: "Equilibrium",
          value : finalData,
          data : details.data,
          color : "#1792ff",
          market : ((finalData/market)*100).toFixed(2)
        }
      }));
    }

    const setParallel = () => {
      setMarkets((net) => ({
        ...net,
        parallel: {
          name: "Parallel",
          value : share,
          color : "#0c3ee3",
          market : ((share/market)*100).toFixed(2)
        }
      }));  
    }

    const getPolkadot = () => {
        let global = Object.entries(markets)?.reduce(function(sum, current) { 
          let vals = current[0] === 'others' ? 0 : current[1].value
            
            return parseFloat(sum + vals) 
        }, 0);

      setMarkets((net) => ({
        ...net,
        others: {
          name: "Others",
          value : global,
          color : "#cccccc",
          market : ((global/market)*100).toFixed(2)
        }
      }));     
    }

    useEffect(()=>{
      setParallel();
      getBinance();
      getKraken();
      getBifrost();
      getEquilibrium();
      getOkex();
      getGate();
      getKucoin();
      
    }, [])

    
  if(Object.keys(markets)?.length === 8) {
    getPolkadot();
  }  

  //Final of To Improve

  return (
    <>

        <div className="flex">
          <div className="box tvl half ml-25 tvl-locked">
            <h3> Total Value Locked </h3>

            {htvl && 
              <Htvl data={htvl} tvl={tvl}/>
            }
              
          </div>

          <div className="box half mr-25" >
            <h3 className="left">Market Share</h3>
            { DailyContrib && htvl &&
              <Share market={marketshare} data={htvl} dailyContrib={DailyContrib}/>
            }
          </div>  
        </div>
        <div className="cdot">
          <div className="box half">
            <h3 className="left">cDOT Market</h3>
            <p className="giant porcentshare">
              <strong>{n(cdots/10000000000)} <small>({m(cdots)} DOT)</small></strong>      
              <em>${n((cdots/10000000000)*tvl?.dotPrice)}</em>
            </p>
          </div>
        </div>
        <div className="box full ">
          <h3>Crowdloan by Projects</h3>
          { raising && parachains && markets &&
            <CrowdloanByProject raising={raising} parachains={parachains} markets={markets}/>
          }
        </div>

        <div className="flex">
          <div className="box small dominance-market">
            <h3>Market Dominance</h3>
            {marketshare &&
              <MarketDominance share={marketshare} markets={markets} />
            }
          </div> 
          <div className="box tvl auctions" >
            <h3>DOT Auctions 
            <span>Total Supply: <strong>1,103,303,471 DOT</strong></span></h3>
            <Dotmarket raise={raising} parachain={parachains} marketShare={marketshare.market}/>
          </div>  
        </div>

        <div className="box full">
          <h3 className="left">Daily Increase of DOT raised during the crowdloan</h3>
          {htvl && 
            <DailyDot contributions={htvl} />
          }
        </div>     
        <div className="box full dailycontribs">
          <h3 className="left">Daily Increase of DOT by Project</h3>
          {DailyContrib && parachains && 
            <DailyDotByProject parachain={parachains} data={DailyContrib}/>
          }
        </div>               
        <div className="box half ml-25">
          <h3 className="left">Crowdloan USD TVL </h3>
          {tvl && DailyContrib &&
            <GlobalTvl price={tvl} dailyContrib={DailyContrib}/>
          }          
        </div>
        <div className="box half mr-25">
          <h3 className="left">Crowdloan DOT TVL</h3>
          {tvl && DailyContrib &&
            <GlobalDotTvl price={tvl} dailyContrib={DailyContrib}/>
          }          
        </div>        
        {DotAmount && tvl && 
          <div className="box full distribution">
            <h3 className="left">Contribution Distribution</h3>
            <DotContributionsFilter dots={DotAmount} price={tvl}/>
          </div>   
        }
          
    </>
  );
}