import React, { useEffect, useState } from 'react';
import {  m  } from '../../data/helpers.jsx';
import { PieChart, Pie, Tooltip, Cell, ResponsiveContainer } from 'recharts';

export default function MarketDominance(data) {


  const share      = data?.share?.share;
  const market     = data?.share?.market; 
  const markets    = data?.markets;


let dataBase2 = Object.values(markets)?.sort((a, b) => b.value - a.value).map((entry, index) => {
  return {
    name: entry.name,
    value : parseFloat(entry.market)
  }
});

let COLORS = Object.values(markets)?.sort((a, b) => b.value - a.value).map((entry, index) => {
  return entry.color
});

  return (  
    <>
    <div className="dominance">
      <div className="chart-dominance">
        <div className="centered score-text">
            {m(data?.share.market)} <small>DOT</small>

        </div>
        <ResponsiveContainer width="100%" height={285}>
        <PieChart >
        <Pie
            data={dataBase2}
            innerRadius={105}
            outerRadius={120}
            cornerRadius={50}
            paddingAngle={1}
            minAngle={8}
            isAnimationActive={false}
            dataKey="value"
          >
            <Tooltip />
            {dataBase2?.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>
        </PieChart>  
        </ResponsiveContainer>  
      </div>
      
      { markets &&
      
        Object.values(markets)?.sort((a, b) => b.value - a.value).map((entry, index) => {
          return (
          <div key={index}> 
            <p className="market-paragraph"> 
              <span style={{'background': entry.color}}></span>
              {entry.name} :
              <strong> {m(entry.value)} { '(' + entry.market + '%)'}</strong>
            </p>
          </div>
          )

      })
    }
   
    </div>

    </>
  )
}


