import React, { useEffect, useState } from 'react';
import Header         from './components/header'
import CrowdloanList  from './components/crowdloanList'
import ReactGA from 'react-ga';

import './styles/styles.css'; 

function App() {

  const [raised        , setRaised        ] = useState();
  const [tvl           , setTvl           ] = useState();
  const [historicalTvl , setHistoricalTvl ] = useState();
  const [dailyContrib  , setDailycontrib  ] = useState();
  const [parachains    , setParachains    ] = useState();

  const getParachains = async () => {
    const res = await fetch("https://cdn.jsdelivr.net/gh/parallel-finance/auction-toolkit@latest/crowdloans/projects/polkadot.json?v3");
    const responseJson = await res.json();
    setParachains(responseJson)
  }

  const getRaised = async () => {
    const res = await fetch("https://auction-service-prod.parallel.fi/crowdloan/polkadot/v2");
    const responseJson = await res.json();
    setRaised(responseJson)
  }

  const dailyContributions = async () => {
    const res = await fetch("https://auction-service-prod.parallel.fi/crowdloan/statistics/polkadot?fundIds=2004-12KHAurRWMFJyxU57S9pQerHsKLCwvWKM1d3dKZVx7gSfkFJ-1,2000-1muqpuFcWvy1Q3tf9Tek882A6ngz46bWPsV6sWiYccnVjKb-0,2012-15kjdKF4hRbYWzLjovPiohT5pVheXhhk8oKHr3DyTaxF2evd-0,2006-1j5YyEGdcPd9BxkzVNNjKkqdi5f7g3Dd7JMgaGUhsMrZ6dZ-0,2002-14e4GmLj5CccWe9Rant9q6yQro1oysqvKiBiHcpCRoscZ1yY-0,2021-12LxQoLA9hebiMSPT3G7ixz73LLuYomMNuRLqX7c9bRWxDFG-1,2019-12EXcpt1CwnSAF9d7YWrh91bQw6R5wmCpJUXPWi7vn2CZFpJ-0,2031-143pzStAtMv3RbYbcHyr2xHipWPkme8VjVgAr4QDQP8d3Xrc-0,2032-14gZicKnmFj3238utrQ6B7CGWBeNGntKUyoUHqoTN85FnHWk-0,2011-14fhPR28n9EHZitNyf6wjYZVBPwKgcgogVjJPTzvCcb8qi9G-0,2026-16LKv69ct6xDzSiUjuz154vCg62dkyysektHFCeJe85xb6X-0,2003-1EdsnniYSKNjHNAvDgvBfRNzKnSzi6kgsHQFCG4PhAyyJWH-0,2013-152deMvsN7wxMbSmdApsds6LWNNNGgsJ8TTpZLTD2ipEHNg3-1,2008-13QrQ7Xos6bseivYW3xRjvi4T2iHihxVnTrQgyHmWGTNv972-0,2027-14r48SVtMrJKxUWD9ijDy8aQU3asTXja8qny9mzXTutdByju-0,2028-13pQt6LnK2tXZtXbiQ6PBYikEoNTi6MXkeBdQCeyR9hm6k1p-1,2017-16RzEcgXVzXXn2gEQbqqp74Pw7MJSb7PKtz29BhVZmpXBKRn-1,2034-1Rp3mJJUxPD1nJ6gf179scdejMSSJb46eYoFyEktR6DYt6z-0")
    const responseJson = await res.json();

    setDailycontrib(responseJson)
  }

  const getTvl = async () => {
    const res = await fetch("https://auction-service-prod.parallel.fi/crowdloan/total-value");
    const responseJson = await res.json();
    setTvl(responseJson)
  }

  const getHistoricalTvl = async () => {
    const res = await fetch("https://auction-service-prod.parallel.fi/crowdloan/historical-tvl");
    const responseJson = await res.json();
    setHistoricalTvl(responseJson)
  }




  useEffect(()=>{
    ReactGA.initialize('UA-212480889-2');
    ReactGA.pageview('Homepage');

    function load() {
      getParachains();
      dailyContributions();
      getRaised();
      getTvl();
      getHistoricalTvl();
      
    }

    load();


      setInterval(() => {
        load()
      }, 15000)
  
  }, [])


  return (
    <>
      <div className="container">
        <Header />
        { raised && parachains && dailyContrib &&
          <CrowdloanList raise={raised} parachain={parachains} dailyContrib={dailyContrib} tvl={tvl} htvl={historicalTvl} />
        }
      </div>
    </>
  );
}

export default App;
